body {
  box-sizing: border-box;
  min-width: 10vw;
  max-width: 726px;
  min-height: 100vh;
  margin: auto;
  display: flex;
}

.calendar {
  flex-wrap: wrap;
  gap: 1px;
  margin: auto;
  padding: 10px;
  display: flex;
}

.calendar__day {
  box-sizing: border-box;
  background-color: #eee;
  border: 1px solid #000;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  transition: all .5s;
  display: none;
}

.calendar__day:hover {
  cursor: pointer;
  background-color: #ffbfcb;
  transform: translateY(-20px);
}

